* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app__wrapper {
  height: 100vh;
}

.app__button {
  position: absolute;
  top: 85%;
  left: 50%;
  transform: translateX(-50%);

  width: 300px;
  height: 50px;
  background-color: orange;
  border: 1px solid #fff;
  color: #fff;
  font-size: 25px;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;

  display: flex;
  align-items: center;
  justify-content: center;
}
.app__text {
  position: absolute;
  top: 29%;
  left: 50%;
  transform: translateX(-50%);
  transition: all 2s ease-in;

  opacity: 0;

  width: 100%;
  height: 100%;
  max-width: 500px;
  max-height: 400px;
  background-color: rgba(166, 211, 219, 0.8);
  border: 2px solid rgb(179, 231, 240);
  border-radius: 20px;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
}

.app__text h1 {
  color: #fff;
  font-weight: bold;
  text-transform: uppercase;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  margin-bottom: 20px;
}

.app__text p {
  color: #fff;
  font-size: 19px;
}

@media (max-width: 500px) {
  .app__text {
    max-width: 400px;
    max-height: 300px;
  }
}
